body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.ColorBox {
  inline-size: 20%;
  block-size: 25%;
  margin: 0 auto;
  display: inline-block;
  position: relative;
  cursor: pointer;
  -webkit-margin-after: -4.5px;
          margin-block-end: -4.5px;
}
.SingleColorPalette .ColorBox {
  block-size: 50%;
}

.light-text {
  color: white;
}
.ColorBox .dark-text {
  color: rgba(0, 0, 0, 0.5);
}
.go-back {
  background-color: black;
}

.copy-button,
.back-button {
  inline-size: 100px;
  block-size: 30px;
  position: absolute;
  display: inline-block;
  inset-block-start: 50%;
  inset-inline-start: 50%;
  -webkit-margin-start: -50px;
          margin-inline-start: -50px;
  -webkit-margin-before: -15px;
          margin-block-start: -15px;
  text-align: center;
  outline: none;
  background: rgba(255, 255, 255, 0.3);
  font-size: 1rem;
  line-height: 30px;
  color: white;
  text-transform: uppercase;
  border: none;
}

.copy-button {
  opacity: 0;
}

.ColorBox:hover .copy-button {
  opacity: 1;
  transition: 0.5s;
}

.box-content {
  position: absolute;
  inline-size: 100%;
  inset-inline-start: 0px;
  inset-block-end: 0px;
  padding: 10px;
  color: black;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: 12px;
}

.see-more {
  background: rgba(255, 255, 255, 0.3);
  position: absolute;
  border: none;
  inset-inline-end: 0px;
  inset-block-end: 0px;
  color: white;
  inline-size: 60px;
  block-size: 30px;
  text-align: center;
  line-height: 30px;
  text-transform: uppercase;
}

.copy-overlay {
  opacity: 0;
  inline-size: 100%;
  height: 100%;
  z-index: 0;
  transition: -webkit-transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
  -webkit-transform: scale(0.1);
          transform: scale(0.1);
}

.copy-overlay.show {
  opacity: 1;
  z-index: 10;
  -webkit-transform: scale(10);
          transform: scale(10);
  position: absolute;
}

.copy-msg {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 4rem;
  -webkit-transform: scale(0.1);
          transform: scale(0.1);
  opacity: 0;
  color: white;
}

.copy-msg.show {
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
  z-index: 25;
  transition: all 0.4s ease-in-out;
  transition-delay: 0.3;
}

.copy-msg h1 {
  font-weight: 400;
  text-shadow: 1px 2px black;
  background: rgba(255, 255, 255, 0.2);
  inline-size: 100%;
  text-align: center;
  -webkit-margin-after: 0;
          margin-block-end: 0;
  padding: 1 rem;
  text-transform: uppercase;
}

.copy-msg p {
  font-size: 2rem;
  font-weight: 100;
}

@media screen and (max-width: 575.98px) {
  .ColorBox {
    inline-size: 100%;
    block-size: 5%;
  }

  .SingleColorPalette .ColorBox {
    block-size: 10%;
  }
  .copy-msg h1 {
    font-size: 3rem;
  }
}

@media screen and (min-width: 576px) and (max-width: 991.98px) {
  .ColorBox {
    inline-size: 50%;
    block-size: 10%;

    overflow-x: hidden;
  }
  .SingleColorPalette .ColorBox {
    block-size: 20%;
  }
}

@media screen and (min-width: 992px) and (max-width: 1199.98px) {
  .ColorBox {
    inline-size: 25%;
    block-size: 20%;

    overflow-x: hidden;
  }
  .SingleColorPalette .ColorBox {
    block-size: 33.333%;
  }
}

.Navbar {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  block-size: 6vh;
}

.logo {
  -webkit-margin-end: 15px;
          margin-inline-end: 15px;
  padding-block: 0;
  padding-inline: 13px;
  font-size: 22px;
  background-color: #eceff1;
  font-family: Roboto;
  block-size: 100%;
  display: flex;
  align-items: center;
}

.logo a {
  text-decoration: none;
  color: black;
}
.rc-slider-track {
  background-color: transparent;
}

.rc-slider-rail {
  height: 8px;
}

.rc-slider-handle,
.rc-slider-handle:hover,
.rc-slider-handle:active,
.rc-slider-handle:focus {
  background-color: green;
  outline: none;
  border: 2px solid green;
  box-shadow: none;
  inline-size: 13px;
  block-size: 13px;
  -webkit-margin-end: -7px;
          margin-inline-end: -7px;
  -webkit-margin-after: -1px;
          margin-block-end: -1px;
}
/* .rc-slider-handle
  .rc-slider-rail */

.select-container {
  -webkit-margin-start: auto;
          margin-inline-start: auto;
  -webkit-margin-end: 1rem;
          margin-inline-end: 1rem;
}

@media screen and (max-width: 575.98px) {
  .logo {
    display: none;
  }

  .slider {
    inline-size: 10px;
  }
}
