.Palette {
  block-size: 100vh;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
}

.Palette-colors {
  block-size: 90%;
}

.slider {
  inline-size: 340px;
  margin-block: 0;
  margin-inline: 10px;
  display: inline-block;
}

.Palette-footer {
  background-color: white;
  block-size: 5vh;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-weight: bold;
}

.emoji {
  align-self: center;
  font-size: 1.25rem;
  margin-inline: 1rem;
  margin-block: 0;
}

@media screen and (max-width: 575.98px) {
  .logo {
    display: none;
  }

  .slider {
    inline-size: 150px;
  }
}

@media screen and (min-width: 576px) and (max-width: 767.98) {
  .slider {
    inline-size: 150px;
  }
}

.page {
  block-size: 100vh;
  inline-size: 100vw;

  transition: opacity 0.5s ease-in-out;
}

.page-enter {
  opacity: 0;
}

.page-enter-active {
  opacity: 1;
}

.page-exit-active {
  opacity: 0;
}

.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 0.01;
  transition: opacity 500ms ease-in;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 250ms ease-in;
}

