.Palette {
  block-size: 100vh;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
}

.Palette-colors {
  block-size: 90%;
}

.slider {
  inline-size: 340px;
  margin-block: 0;
  margin-inline: 10px;
  display: inline-block;
}

.Palette-footer {
  background-color: white;
  block-size: 5vh;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-weight: bold;
}

.emoji {
  align-self: center;
  font-size: 1.25rem;
  margin-inline: 1rem;
  margin-block: 0;
}

@media screen and (max-width: 575.98px) {
  .logo {
    display: none;
  }

  .slider {
    inline-size: 150px;
  }
}

@media screen and (min-width: 576px) and (max-width: 767.98) {
  .slider {
    inline-size: 150px;
  }
}
